 @use "icons";
 @use "mixins" as *;

 .icon,
 %icon {
 	display: flex;
 	border: none;
 	user-select: none;
 	mask-size: contain;
 	mask-repeat: no-repeat;
 	mask-position: center;
 	mask-image: var(--mask);
 	flex-shrink: 0;
 	transition-duration: var(--default-duration);
 	transition-timing-function: ease;
 	transition-property: opacity, background-color;
 	width: rem(var(--icon-width, var(--icon-size, 24)));
 	// TODO height: rem(var(--icon-width, var(--icon-size, 24)));
 	aspect-ratio: 1;
 	max-width: 100%;
 	background-color: var(--icon-color, currentColor);
 }