@use "container" as *;
@use "mixins" as *;
@use "breakpoint" as *;

.page {
	&:has(&__aside) {
		@extend %container;
	}

	&__container {
		display: grid;
		grid-auto-flow: column;
		grid-auto-columns: 1fr;
		column-gap: var(--column-gap);
		row-gap: 32px;

		@media (max-width: $tablet) {
			grid-auto-flow: row;
		}
	}

	&__aside {
		@media (min-width: $tablet) {
			grid-column: span 3;
		}

		&__sticky {
			position: sticky;
			top: 32px;
			bottom: 32px;
		}
	}

	&__content {
		display: flex;
		flex-direction: column;
		gap: 64px;
		min-width: 0;

		@media (min-width: $tablet) {
			grid-column: span 9;
		}

		@media (max-width: $tablet) {
			gap: 32px;
		}
	}
}