@use "mixins" as *;

.catalog {
	display: flex;
	flex-direction: column;
	gap: 24px;

	&__header {
		display: flex;
		flex-direction: column;
		gap: 8px;

		:is(h1, h2, h3, h4, h5, h6) {
			color: var(--color-primary);
		}

		a {
			@include hoverType1;
		}
	}

	&__controls {}

	&__items {}

	&__footer {}
}