@use "mixins" as *;
@use "splide" as *;
@use "breakpoint" as *;
@use "button-link" as *;
@use "custom-list" as *;

.collections-special {
	color: var(--color-text-alt);
	padding-top: 128px;
	padding-bottom: 128px;
	@extend %splide--float-arrows-block;

	@media (max-width: $laptop) {
		padding-top: 64px;
		padding-bottom: 64px;
	}

	@media (max-width: $mobile) {
		padding-top: 32px;
		padding-bottom: 32px;
	}

	&__inner {
		display: flex;
		flex-direction: column;
		gap: gap-scale(2);
	}

	&__card {
		background-color: var(--color-text-alt);
		color: var(--color-text);
		border-radius: var(--default-border-radius);
		display: flex;
		flex-direction: column;
		align-items: stretch;
		gap: gap-scale(1);
		padding: gap-scale(1.5) gap-scale(1);
		height: 100%;
		@include transition;
		transition-property: color;

		&:is(:hover, :focus, :focus-within) {
			color: var(--color-accent);
		}

		&__title {
			@include transition;
			transition-property: color;
			color: var(--color-primary);
		}

		&__img {
			display: flex;
			justify-content: center;
		}

		&__link {
			@extend %button-link;
		}

		&__text {
			display: flex;
			flex-direction: column;
			gap: inherit;

			ul {
				@extend %custom-list;
				color: var(--color-primary);
			}
		}

		&__footer {
			display: flex;
			justify-content: flex-end;
			margin-top: auto;
		}
	}

	.splide {
		@extend %splide--float-arrows;

		&__arrow {
			@extend %splide__arrow--light;
		}
	}
}