@use "mixins" as *;
@use "breakpoint" as *;
@use "icon" as *;
@use "button" as *;
@use "accordion" as *;

.accordion-nav {
	--sub-left: 30px;
	--radius: var(--default-border-radius-small);
	display: flex;
	flex-direction: column;
	gap: 12px;

	&__item {
		display: block;

		%last {
			border-bottom: var(--default-border);
			border-radius: 0 0 var(--radius) var(--radius);
		}

		&:last-child &__self {
			@extend %last;
		}

		&:first-child &__self {
			border-radius: var(--radius) var(--radius) 0 0;
		}

		&__self {
			border-top: var(--default-border);
			border-left: var(--default-border);
			border-right: var(--default-border);
			position: relative;
			padding: 12px 12px 12px 20px;
			background-color: var(--bgc);
			display: flex;
			align-items: center;
			justify-content: space-between;
			flex-wrap: wrap;

			a {
				@include hoverType1;
			}
		}

		&__self:has(+ &__list:focus-within, .is-active) {
			border-bottom: var(--default-border);

			&::before {
				content: "";
				position: absolute;
				left: var(--sub-left);
				top: 100%;
				width: 14px;
				height: 14px;
				clip-path: polygon(100% 0, 0 100%, 100% 100%);
				transform: translateY(-50%) rotate(45deg);
				border: var(--default-border);
				background-color: var(--color-text-alt);
			}
		}

		&__toggler {
			@extend %button-text;
			@extend %accordion__toggler;

			&.is-active::after {
				@layer {
					& {
						transform: scaleY(-1);
					}
				}
			}

			&::after {
				content: "";
				@extend %icon, %icon--chevron_down;
			}
		}

		&__list {
			@extend %accordion__wrapper;
		}

		&__content {
			@extend %accordion__content;
		}
	}

	&__list {
		@extend %accordion__content;
	}

	&__wrapper {
		@extend %accordion__wrapper;

		@media (min-width: $tablet) {
			@include accordion-wrapper-opened;
		}
	}

	&__sub {
		display: flex;
		flex-direction: column;
		gap: 8px;
		padding: 14px var(--sub-left);

		&__item {
			display: flex;

			a {
				@include hoverType2;
			}
		}
	}

	&:not(.is-active) &__list {
		@media (max-width: $tablet) {
			display: none;
		}
	}

	&.is-active &__toggler::after {
		transform: scaleY(-1);
	}

	&__toggler {
		border: var(--default-border);
		border-radius: var(--radius);
		display: flex;
		justify-content: center;
		padding: 12px;
		color: var(--base-color);

		&::after {
			content: "";
			@extend %icon, %icon--chevron_down;
		}

		@media (min-width: $tablet) {
			display: none;
		}
	}
}