@use "custom-list" as *;
@use "breakpoint" as *;
@use "mixins" as *;

.text,
%text {
	--gap-medium: 40px;
	--gap-small: 24px;
	--gap-xsmall: 12px;

	@media (max-width: $mobile) {
		--gap-medium: 24px;
		--gap-small: 16px;
	}

	%edge-padding {
		&:first-child {
			margin-top: 0;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}

	ul,
	ol {
		@extend %custom-list;
		margin: var(--gap-xsmall) 0;
	}

	img {
		max-width: 100%;
		height: auto !important;
		// max-width: max(25vw, 400px);
		// max-height: max(25vw, 400px);
	}

	iframe {
		width: 100% !important;
		height: auto !important;
		aspect-ratio: 16 / 9;
	}

	h1,
	h2,
	h3,
	h4 {
		color: var(--color-primary);
		margin-bottom: var(--gap-small);
		@extend %edge-padding;
	}

	h1 {
		// margin-bottom: var(--gap-small);
	}

	h1,
	h2 {
		margin-top: var(--gap-medium);
	}

	h3,
	h4 {
		margin-top: var(--gap-small);
	}

	p {
		margin-top: var(--gap-xsmall);
		margin-bottom: var(--gap-xsmall);
		@extend %edge-padding;
	}

	a {
		@include hoverType2;
	}
}