@use "icon" as *;
@use "button" as *;
@use "accordion" as *;
@use "mixins" as *;

.accordion-list {
	display: flex;
	flex-direction: column;
	gap: 16px;
	--toggler-gap: 12px;
	--toggler-icon-size: 24px;
	--inner-offset: calc(var(--toggler-gap) + var(--toggler-icon-size));

	&__header {
		display: flex;
		justify-content: flex-end;
	}

	&__toggler {
		cursor: pointer;
		--icon-size: 28;
		@extend %button-text;

		&::after {
			content: "";
			@extend %icon, %icon--expand_all;
		}
	}

	&:has([data-accordion].is-active) &__toggler::after {
		@extend %icon--collapse_all;
	}

	&__items {
		border-bottom: 1px solid var(--default-border-color);
	}

	&__item {

		&:not(:first-child),
		& & {
			padding-top: 12px;
			border-top: 1px solid var(--default-border-color);
		}

		& & {
			padding-left: 24px;
		}

		&__header {
			display: flex;
			align-items: center;
			gap: 12px;
			padding-bottom: 12px;
		}

		&:not(:has(&__content)) &__header {
			padding-left: var(--inner-offset);
		}

		&__toggler {
			@extend %button-text;
			@extend %accordion__toggler;

			&::before {
				content: "";
				@extend %icon, %icon--expand;
			}

			&.is-active::before {
				@extend %icon--collapse;
			}
		}

		&__wrapper {
			@extend %accordion__wrapper;
		}

		&__content {
			@extend %accordion__content;
		}
	}
}