@use "font" as *;
@use "icon" as *;
@use "mixins" as *;

.custom-input,
%custom-input {
	display: grid;
	grid-template-columns: 1fr auto;
	width: 100%;
	gap: 6px 0;
	@extend %font-3;

	&__label {
		grid-column: 1 / -1;
		// @extend %font-2;
		// color: var(--color-primary);
	}

	&:has(:required) &__label {
		&::after {
			content: "*";
		}
	}

	&__button {
		display: flex;
		align-items: center;
		justify-content: center;
		border: var(--default-border);
		border-radius: var(--default-border-radius-small);
		height: 100%;
		min-width: var(--input-height);
		--icon-size: 28;
		--icon-color: var(--color-primary);
		transition: border-color ease var(--default-duration);
		margin-left: 4px;
		@include hoverTypeBorder;
		padding: 0 4px;

		&--eye {
			&::before {
				content: "";
				@extend %icon, %icon--eye;
			}

			&.is-active {
				&::before {
					@extend %icon--eye_off;
				}
			}
		}
	}

	&__link {
		@include hoverType3;
	}

	&__caption {
		grid-column: 1 / -1;
		@extend %font-4;
	}

	&[data-validation-message]::after {
		height: 0;
		content: attr(data-validation-message);
		grid-column: 1 / -1;
		@extend %font-4;
		color: var(--color-accent-alt1);
	}

	textarea {
		resize: vertical;
		min-height: var(--input-height);
		height: calc(var(--input-height) * 2);
	}

	input {
		height: var(--input-height);
	}

	&--small {
		input {
			@extend %font-2;
			height: var(--input-height-small);
		}
	}

	input,
	textarea {
		background-color: var(--color-text-alt);
		width: 100%;
		border: var(--default-border);
		border-radius: var(--default-border-radius-small);
		padding: 6px 12px;
		transition-property: background-color, border-color;
		transition-timing-function: ease;
		transition-duration: var(--default-duration);

		&::placeholder {
			color: var(--color-text2);
		}

		&:read-only {
			background-color: var(--color-primary-lighter);
			// TODO подобрать цвет для тем
		}

		&:not(:disabled) {

			&:hover,
			&:focus,
			&:not(:placeholder-shown) {
				border-color: var(--color-primary);
			}

			&:not(:focus):not(:hover) {

				// &:invalid:not(:placeholder-shown),
				&.is-invalid {
					border-color: var(--color-accent-alt1);
					background-color: color-mix(in srgb, var(--color-accent-alt1) 10%, #0000);
				}
			}
		}
	}
}