@use "font" as *;
@use "icon" as *;
@use "button" as *;
@use "mixins" as *;

.webinar-list {
	display: flex;
	flex-direction: column;
	gap: gap-scale(1.5);

	&__item {
		padding: gap-scale(1) gap-scale(2);
		border: var(--default-border);
		border-radius: var(--default-border-radius-small);
		display: flex;
		flex-direction: column;
		gap: 12px;

		&__title {
			@extend %h5;
			@include hoverType1;
		}

		&__info {
			display: flex;
			flex-wrap: wrap;
			gap: gap-scale(0.5);
		}

		&__date {
			display: flex;
			align-items: center;
			gap: 4px;

			&::before {
				content: "";
				@extend %icon, %icon--calendar;
				color: var(--color-primary);
			}

			em {
				color: var(--color-accent);
			}
		}

		&__time {
			display: flex;
			align-items: center;
			gap: 4px;

			&::before {
				content: "";
				@extend %icon, %icon--clock;
				color: var(--color-primary);
			}
		}

		&__presenters {
			// display: grid;
			// grid-template-columns: minmax(auto, max-content) minmax(auto, max-content);
			display: flex;
			flex-wrap: wrap;
			gap: gap-scale(1);
		}

		&__presenter {
			display: grid;
			grid-template-columns: 56px auto;
			grid-template-rows: auto 1fr;
			gap: 4px 12px;

			&__img {
				grid-row: span 2;
			}

			&__post {
				display: flex;
				gap: 4px;
				align-items: center;
				color: var(--color-primary);

				&::before {
					content: "";
					@extend %icon, %icon--person;
				}
			}
		}

		&__button {
			max-width: max-content;
			@extend %button;

			&.is-active {
				@extend %button--accent;
			}
		}
	}
}