 :root {
 	font-family: "GolosText", sans-serif;
 }

 body {
 	@extend %font-3;
 }

 input[type="text"],
 input[type="password"],
 input[type="email"],
 input[type="tel"],
 input[type="number"],
 input[type="date"],
 textarea,
 em {
 	appearance: textfield;
 	font: inherit;

 	&::-webkit-inner-spin-button {
 		-webkit-appearance: none;
 	}
 }

 h1,
 .h1,
 %h1 {
 	font-size: rem(32);
 	font-weight: 400;
 	line-height: 1.2;

 	@media (max-width: $mobile) {
 		font-size: rem(26);
 	}
 }

 h2,
 .h2,
 %h2 {
 	font-size: rem(26);
 	font-weight: 400;
 	line-height: 1.2;

 	@media (max-width: $mobile) {
 		font-size: rem(24);
 	}
 }

 h3,
 .h3,
 %h3 {
 	font-size: rem(24);
 	font-weight: 400;
 	line-height: 1.2;

 	@media (max-width: $mobile) {
 		font-size: rem(22);
 	}
 }

 h4,
 .h4,
 %h4 {
 	font-size: rem(22);
 	line-height: 1.2;
 	font-weight: 400;

 	@media (max-width: $mobile) {
 		font-size: rem(20);
 	}
 }

 h5,
 .h5,
 %h5 {
 	font-size: rem(20);
 	line-height: 1.2;
 	font-weight: 400;

 	@media (max-width: $mobile) {
 		font-size: rem(18);
 	}
 }

 h6,
 .h6,
 %h6 {
 	font-size: rem(18);
 	line-height: 1.2;
 	font-weight: 400;

 	@media (max-width: $mobile) {
 		font-size: rem(16);
 	}
 }

 .font-2,
 %font-2 {
 	font-weight: 400;
 	font-size: rem(14);
 	line-height: 1.5;

 	@media (max-width: $mobile) {
 		font-size: rem(12);
 	}
 }

 .font-3,
 %font-3 {
 	font-weight: 400;
 	font-size: rem(16);
 	line-height: 1.5;

 	@media (max-width: $mobile) {
 		font-size: rem(14);
 	}
 }

 .font-4,
 %font-4 {
 	font-weight: 400;
 	font-size: rem(12);
 	line-height: 1.5;
 }

