.accordion,
%accordion {

	// все баяны сделать экстендами
	&__toggler {
		user-select: none;
		cursor: pointer;

		&:disabled,
		&.is-disabled {
			cursor: default;
			pointer-events: none;
		}
	}

	&__content {
		min-height: 0;
	}

	&__wrapper {
		display: grid;
		grid-template-rows: 0fr;
		overflow: hidden;
		transition: grid-template-rows ease var(--default-duration);

		&:not(.is-active) {
			visibility: hidden;
		}

		&.is-active {
			grid-template-rows: 1fr;
		}
	}
}

@mixin accordion-opened {
	.accordion__wrapper {
		visibility: visible;
		grid-template-rows: 1fr;
	}
}

@mixin accordion-wrapper-opened {
	grid-template-rows: 1fr !important;
	visibility: visible !important;
}