@use "select-simple" as *;

.form {
	display: flex;
	flex-direction: column;
	gap: 16px;

	&__row {
		display: grid;
		grid-auto-columns: 1fr;
		grid-auto-flow: column;
		gap: inherit;
	}

	&__item {}

	&__element-group {
		display: flex;
		flex-direction: column;
		gap: 8px;
		padding: 8px 12px;
		border: var(--default-border);
		border-radius: var(--default-border-radius-small);

		&:not(:has(legend)) {
			border: none;
			padding: 0;
		}

		legend {
			padding: 0 8px;
		}

		&__items {
			display: flex;
			flex-wrap: wrap;
			gap: 16px;
		}
	}

	&__controls {
		display: flex;
		gap: 12px;
		flex-wrap: wrap;
	}

	.select-simple {
		@extend %select-simple--big;
	}
}