@use "font" as *;
@use "button" as *;
@use "icon" as *;

.controls {
	display: flex;
	flex-direction: column;
	gap: 16px;

	&__row {
		display: flex;
		flex-wrap: wrap;
	}

	&__group {
		display: flex;
		gap: 16px;
		padding-left: 8px;

		&:not(:last-child):not(:nth-last-child(2)) {
			padding-right: 8px;
			border-right: 1px solid #000;
		}

		&:last-child {
			margin-left: auto;
		}
	}

	&__item {
		display: flex;
		align-items: center;
		gap: 8px;
		color: var(--color-text1);
		flex-wrap: wrap;
		@extend %font-2;

		em {
			font-weight: 700;
		}
	}

	&__button {
		@extend %button-text;

		&__collapse {
			&::after {
				content: "";
				@extend %icon, %icon--collapse_all;
			}

			&.is-active {
				@extend %icon, %icon--expand_all;
			}
		}
	}
}