@use "font" as *;

.labels {
	display: flex;
	flex-wrap: wrap;
	gap: 6px;
}

.label {
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	height: 22px;
	min-height: min-content;
	min-width: min-content;

	span {
		display: block;
		padding: 0 7px;
		color: var(--color-text-alt);
		@extend %font-4;
		font-weight: 700;
	}

	img {
		height: 100%;
		width: auto;
	}

	&--buy {
		background-color: var(--color-accent-alt1);
	}

	&--new {
		background-color: var(--color-accent);
	}

	&--perpetual {
		background-color: var(--color-primary-alt);
	}

	&--base {
		background-color: var(--color-accent-alt2);
	}

	&--free {
		background-color: var(--color-accent-alt);
	}

	&--exclusive {
		background-color: var(--color-primary);
	}

	&--fpu {
		background-color: var(--color-accent-alt3);
	}

	&--efu {
		background-color: var(--color-accent-alt5);
	}

	&--fpup {
		background-color: var(--color-accent-alt4);
	}
}