@use "splide" as *;
@use "mixins" as *;
@use "font" as *;

.books-slider {
	&__slide {
		display: flex;
		flex-direction: column;
		gap: 12px;
		@include hoverType1;

		&__img {
			height: rem(230);
			width: auto;
			box-shadow: 0 4px 15px rgba(0, 0, 0, .1);
		}

		&__title {
			width: 0;
			min-width: 100%;
			@include text-line-clip(3);
			@extend %font-2;
		}
	}

	.splide {
		@extend %splide--float-arrows;
	}
}