 @use "font" as *;

 //  TODO придумать как вертеть картинки в несерых темах
 :root {
 	--base-hue: 207;
 	--color-white: #FFFFFF;
 	--color-black: #2E3239;
 	--color-orange: hsl(calc(var(--base-hue) + 180), 91%, 54%);
 	--color-blue: hsl(var(--base-hue), 53%, 46%);
 	--color-blue1: hsl(var(--base-hue), 60%, 70%);
 	--color-blue2: hsl(var(--base-hue), 75%, 90%);
 	--color-blue3: hsl(var(--base-hue), 100%, 97%);
 	--color-red: hsl(0, 52%, 46%);
 	--color-grey: hsl(210, 1%, 29%);
 	--color-grey1: hsl(0, 0%, 69%);
 	--color-green: hsl(136, 53%, 43%);
 	--color-aqua: hsl(187, 52%, 46%);
 	--color-yellow: hsl(50, 100%, 88%);
 	--color-purple: hsl(338, 41%, 54%);
 	--color-purple1: hsl(278, 41%, 54%);
 	--color-brown: hsl(38, 41%, 54%);
 }

 :root {
 	--color-text: var(--color-black);
 	--color-text1: var(--color-grey);
 	--color-text2: var(--color-grey1);
 	--color-text-alt: var(--color-white);
 	--color-primary: var(--color-blue);
 	--color-primary-alt: var(--color-blue1);
 	--color-primary-light: var(--color-blue2);
 	--color-primary-lighter: var(--color-blue3);
 	--color-accent: var(--color-orange);
 	--color-accent-alt: var(--color-green);
 	--color-accent-alt1: var(--color-red);
 	--color-accent-alt2: var(--color-aqua);
 	--color-accent-alt3: var(--color-purple);
 	--color-accent-alt4: var(--color-purple1);
 	--color-accent-alt5: var(--color-brown);
 	--color-half-transparent: rgba(0, 0, 0, .7);

 	&:has(.theme1) {
 		filter: grayscale(1);
 		--color-text: var(--color-black);
 		--color-text1: var(--color-grey);
 		--color-text2: var(--color-grey);
 		--color-text-alt: var(--color-white);
 		--color-primary: var(--color-black);
 		--color-primary-alt: var(--color-grey);
 		--color-primary-light: var(--color-grey1);
 		--color-primary-lighter: var(--color-grey1);
 		--color-accent: var(--color-black);
 		--color-accent-alt: var(--color-black);
 		--color-accent-alt1: var(--color-black);
 		--color-accent-alt2: var(--color-black);
 		--color-accent-alt3: var(--color-black);
 		--color-accent-alt4: var(--color-black);
 		--color-accent-alt5: var(--color-black);
 		--color-half-transparent: var(--color-primary-lighter);

 		a,
 		button {
 			text-decoration: underline !important;

 			* {
 				text-decoration: inherit !important;
 			}

 			&:disabled,
 			&.is-disabled,
 			&:is(:hover, :focus, :focus-within) {
 				text-decoration: none !important;
 			}
 		}

 		*:focus {
 			outline: var(--default-border);
 		}
 	}

 	&:has(.theme2) {
 		background-color: var(--color-black);
 		filter: grayscale(1);
 		// font-size: 20px;
 		--color-text: var(--color-white);
 		--color-text1: var(--color-grey);
 		--color-text2: var(--color-grey1);
 		--color-text-alt: var(--color-black);
 		--color-primary: var(--color-white);
 		--color-primary-alt: var(--color-grey1);
 		--color-primary-light: var(--color-grey1);
 		--color-primary-lighter: var(--color-grey1);
 		--color-accent: var(--color-white);
 		--color-accent-alt: var(--color-white);
 		--color-accent-alt1: var(--color-white);
 		--color-accent-alt2: var(--color-white);
 		--color-accent-alt3: var(--color-white);
 		--color-accent-alt4: var(--color-white);
 		--color-accent-alt5: var(--color-white);
 	}
 }

 html {
 	color: var(--color-text);
 }

 a {
 	color: inherit;
 }