@use "button" as *;
@use "font" as *;
@use "breakpoint" as *;
@use "accordion" as *;
@use "icon" as *;
@use "mixins" as *;

.collections {
	--icon-size: 24;
	--toggler-gap: 12px;
	--inner-offset: calc(var(--icon-size) * 1px + var(--toggler-gap));
	--py: 16px;

	& & {
		padding-left: var(--inner-offset);
	}

	&:not(& &) {
		border-bottom: var(--default-border);
		padding-bottom: var(--py);
	}

	& & &__item {
		padding-top: var(--py);
		border-bottom-style: dashed;
	}

	&__item {
		border-block-width: 0;
		border-block-color: var(--default-border-color);
		border-bottom-style: solid;

		&:not(:last-child) {
			padding-bottom: var(--py);
			border-width: var(--default-border-width);
		}

		&:not(:first-child) {
			padding-top: var(--py);
		}
	}

	&__collection {
		padding-left: var(--inner-offset);
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: gap-scale(0.5);

		@media (max-width: $mobile) {
			flex-direction: column;
			align-items: stretch;
		}

		&__info {
			display: flex;
			flex-direction: column;
			gap: 6px;

			@media (max-width: $mobile) {
				justify-content: space-between;
			}
		}

		&__buy {
			display: flex;
			align-items: center;
			gap: gap-scale(1);

			@media (max-width: $mobile) {
				align-self: flex-end;
			}
		}

		&__title {
			@extend %h5;
			@include hoverType1;
		}

		&__price {
			font-weight: 500;
		}

		&__button {
			@extend %button;

			&.is-active {
				@extend %button--accent-alt;
			}
		}
	}

	&__group {
		@extend %accordion;

		&__title {
			@extend %accordion__toggler;
			@extend %button-text, %h4;
			gap: var(--toggler-gap);

			&::before {
				content: "";
				@extend %icon, %icon--expand;
			}

			&.is-active {
				&::before {
					@extend %icon, %icon--collapse;
				}
			}
		}

		&__wrapper {
			@extend %accordion__wrapper;
		}

		&__inner {
			@extend %accordion__content;
		}
	}
}