.m31 {
	position: relative;
	background-color: #000007;
	display: grid;

	> :nth-child(n) {
		grid-row: 1;
		grid-column: 1;
		min-width: 0;
	}

	&__layer1,
	&__layer2 {
		display: contents;

		&::before,
		&::after {
			grid-row: 1;
			grid-column: 1;
			content: "";
			background-size: cover;
			display: block;
			pointer-events: none;
			background-position: top calc(50% + var(--scrolled) / var(--scroll-ratio)) center;
		}
	}

	&__layer1 {
		&::before {
			background-image: url(/src/assets/static/img/space/1.webp);
			background-repeat: no-repeat;
			--scroll-ratio: 3;
		}

		&::after {
			background-image: url(/src/assets/static/img/space/2.webp);
			--scroll-ratio: 4;
		}
	}

	&__layer2 {
		&::before {
			background-image: url(/src/assets/static/img/space/3.webp);
			background-repeat: no-repeat;
			--scroll-ratio: 3;
		}

		&::after {
			background-image: url(/src/assets/static/img/space/4.webp);
			--scroll-ratio: 2;
		}
	}
}