@use "icon" as *;
@use "font" as *;
@use "text" as *;
@use "mixins" as *;
@use "breakpoint" as *;
@use "button" as *;

.webinar {
	display: grid;
	grid-template-columns: 9fr 3fr;
	gap: gap-scale(1.5) var(--column-gap);

	@media (max-width: $mobile) {
		grid-template-columns: 1fr;
	}

	&__header {
		grid-column: 1 / -1;
		display: grid;
		border-radius: var(--default-border-radius);
		color: var(--color-text-alt);
		min-width: 0;

		&__bg,
		&__content {
			border-radius: inherit;
			grid-row: 1;
			grid-column: 1;
		}

		&__bg {
			width: 0;
			height: 0;
			min-width: 100%;
			min-height: 100%;
			object-fit: cover;
			@include themeHide;
		}

		&__content {
			display: flex;
			flex-direction: column;
			gap: gap-scale(1.5);
			background-color: color-mix(in srgb, var(--color-text1) 70%, #0000);
			padding-top: gap-scale(3);
			padding-left: gap-scale(2);
			padding-right: gap-scale(2);
			padding-bottom: gap-scale(1.5);
		}

		& a:not(.webinar__button) {
			color: var(--color-accent);

			&:is(:hover, :focus, :focus-within) {
				text-decoration: underline;
			}
		}
	}

	&__content {
		display: flex;
		flex-direction: column;
		gap: gap-scale(1.5);
		min-width: 0;
	}

	&__aside {
		min-width: 0;
	}

	&__info {
		display: flex;
		flex-wrap: wrap;
		gap: 16px;
		@extend %h5;
	}

	&__button {
		@extend %button, %button--accent;
		max-width: max-content;
	}

	&__date {
		display: flex;
		align-items: center;
		gap: 4px;

		&::before {
			content: "";
			@extend %icon, %icon--calendar;
		}

		em {
			color: var(--color-accent);
		}
	}

	&__time {
		display: flex;
		align-items: center;
		gap: 4px;

		&::before {
			content: "";
			@extend %icon, %icon--clock;
		}
	}

	&__presenters {
		display: flex;
		flex-direction: column;
		gap: gap-scale(1.5);

		@media (max-width: $mobile) {
			flex-direction: row;
			flex-wrap: wrap;
		}
	}

	&__presenter {
		@media (max-width: $mobile) {
			flex: 1 1 0;
			max-width: 400px;
			min-width: 150px;
		}

		&__img {
			border-radius: var(--default-border-radius) var(--default-border-radius) 0 0;
		}

		&__info {
			border-radius: 0 0 var(--default-border-radius) var(--default-border-radius);
			color: var(--color-text-alt);
			background-color: var(--color-primary);
			padding: gap-scale(0.75) gap-scale(1);
			display: flex;
			flex-direction: column;
			gap: gap-scale(0.5);
			@extend %font-2;

			small {
				@extend %font-4;
			}
		}
	}

	&__video {
		iframe {
			display: block;
			width: 100% !important;
			height: auto !important;
			aspect-ratio: 16 / 9;
			border-radius: var(--default-border-radius);
		}
	}

	&__text,
	&__main {
		@extend %text;
	}

	&__main {
		padding: gap-scale(2) gap-scale(1.5);
		background-color: var(--color-primary-lighter);
		border-radius: var(--default-border-radius);

		&__extra {
			margin-top: gap-scale(2);
		}
	}
}