@use "mixins" as *;
@use "font" as *;
@use "button" as *;
@use "icon" as *;

// TODO
.select-simple,
%select-simple {
	display: flex;
	flex-direction: column;
	gap: 6px 0;
	@extend %font-3;

	&--big &__select {
		min-height: var(--input-height);
	}

	&__select {
		background-color: var(--color-text-alt);
		border: var(--default-border);
		border-radius: var(--default-border-radius-small);
		min-height: var(--input-height-small);
		padding: 2px 4px 2px 8px;
		display: flex;
		align-items: center;
		color: var(--color-primary);
		@include hoverTypeBorder;

		&::after {
			content: "";
			@extend %icon, %icon--chevron_down;
			--icon-size: 20;
			pointer-events: none;
		}
	}

	select {
		@extend %font-2;
		background-color: inherit;
		margin: -2px -24px -2px -8px;
		padding: 2px 24px 2px 8px;
		color: inherit;
		cursor: pointer;
		flex-grow: 1;

		@layer uset {
			& {
				all: unset;
			}
		}
	}
}