@use "font" as *;
@use "breakpoint" as *;

.tooltip {
	width: max-content;
	max-width: min(300px, 90vw);
	position: fixed;
	top: var(--top);
	left: var(--left);
	right: var(--right);
	bottom: var(--bottom);
	opacity: 0;
	z-index: 999;
	pointer-events: none;
	overflow-wrap: break-word;
	text-align: center;
	filter: drop-shadow(0 0 6px color-mix(in srgb, var(--color-primary) 35%, #0000));
	@extend %font-2;

	@media (max-width: $tablet) {
		display: none;
	}

	&.is-active {
		opacity: 1;
	}

	&__text {
		padding: 2px 6px;
		background-color: var(--color-text-alt);
		border-radius: var(--default-border-radius-small);
		color: var(--color-text);
	}

	&__arrow {
		position: absolute;
		width: 12px;
		height: 12px;
		background-color: var(--color-text-alt);
		z-index: -1;
		pointer-events: none;
		transform: rotate(45deg);
		top: var(--arrow-top);
		left: var(--arrow-left);
		bottom: var(--arrow-bottom);
		right: var(--arrow-right);
	}
}