@use "custom-list" as *;
@use "button" as *;
@use "font" as *;
@use "mixins" as *;
@use "breakpoint" as *;

// TODO доделать
.advantages {
	padding-top: 128px;
	padding-bottom: 128px;
	position: relative;

	@media (max-width: $laptop) {
		padding-top: 64px;
		padding-bottom: 64px;
	}

	@media (max-width: $mobile) {
		padding-top: 32px;
		padding-bottom: 32px;
	}

	&__inner {
		display: flex;
		flex-direction: column;
		gap: 32px;
	}

	&__title {
		color: var(--color-text-alt);

		strong {
			font: inherit;
			color: var(--color-primary);
		}
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-auto-rows: 1fr;
		gap: gap-scale(1.5);

		@media (max-width: $laptop) {
			grid-template-columns: repeat(2, 1fr);
		}

		@media (max-width: $mobile) {
			grid-template-columns: repeat(1, 1fr);
			grid-auto-rows: auto;
		}
	}

	&__item {
		display: grid;
		position: relative;

		&__content {
			backface-visibility: hidden;
			transition: all .3s ease;
			--p-small: 32px;
			--p-big: 72px;
			padding: gap-scale(1.5) gap-scale(1);
			border-radius: var(--default-border-radius);
			display: flex;
			flex-direction: column;
			align-items: center;
			gap: 12px;
			background-color: var(--color-text-alt);
			position: relative;
		}

		&:has(&__promo) &__content {
			// background-color: var(--color-primary);
			// color: var(--color-text-alt);
		}

		&:has(&__promo) &__title {
			color: var(--color-primary);
			@extend %h1;
			font-weight: 700;
		}

		&__title {
			text-align: center;
			color: var(--color-primary-alt);
			font-weight: 700;
		}

		&__text {
			ul {
				@extend %custom-list;
			}
		}

		&__promo {
			display: flex;
			flex-direction: column;
			align-items: center;
			flex-grow: 1;

			.icon {
				margin-bottom: auto;
				--icon-size: 120;
				--icon-color: var(--color-accent);
			}

			em {
				@extend %h1;
			}

			small {
				@extend %font-4;
				margin: 12px 0;
			}

			a {}

			&__link {
				margin-top: auto;
				@extend %button, %button--accent;
			}
		}
	}
}