@use "font" as *;
@use "breakpoint" as *;
@use "accordion" as *;
@use "icon" as *;
@use "mixins" as *;
@use "button" as *;

.groups {
	--icon-size: 24;
	--toggler-gap: #{gap-scale(0.5)};
	--inner-offset: calc(var(--icon-size) * 1px + var(--toggler-gap));
	--py: 16px;

	&__item {
		@extend %accordion;

		&:not(:first-child),
		& & {
			padding-top: var(--py);
		}

		& & {
			padding-left: var(--inner-offset);
		}

		&__header {
			padding-bottom: var(--py);
			display: flex;
			align-items: center;
			gap: gap-scale(0.5);
			border-bottom: var(--default-border);

			@media (max-width: $mobile) {
				flex-wrap: wrap;
			}
		}

		&__header:not(:has(&__button)) {
			padding-left: var(--inner-offset);
		}

		&__title {
			@include hoverType2;
			@include transition;
			transition-property: font;

			.is-active + & {
				font-weight: 700;
			}
		}

		&__button {
			@extend %button-text;
			@extend %accordion__toggler;

			&::before {
				content: "";
				@extend %icon, %icon--expand;
			}

			&.is-active {
				&::before {
					@extend %icon, %icon--collapse;
				}
			}
		}

		&__content {
			@extend %accordion__wrapper;
		}

		&__sub {
			@extend %accordion__content;
		}

		&__controls {
			margin-left: auto;
		}
	}
}